import React, { useMemo } from 'react'
import { Redirect, useParams  } from 'react-router-dom'
import { getHeroById } from '../../selectors/getHeroByid';

export const HerosScreen = ({history}) => {
    
    const { heroeId } = useParams();

    const hero = useMemo(() => getHeroById(heroeId), [heroeId]);

    if (!hero) {
        return <Redirect to="/" />
    }
    
    const handleReturn = ()=>{
        if (history.length <=2 ) {
            history.push('/')
        } else{
            history.goBack()
        }
    }

    const {
        nombre,
        posicion,
        profesion,
        instagram,
        biografia
    } = hero;

    return (
        <div className="row mt-5">
            <div className="col-4">
                <img src={`../assets/heroes/${heroeId}.jpg`}
                    alt={nombre}
                    className="img-thumbnail animate__animated animate__fadeInLeft"/>
            </div>
            <div className="col-8">
                <h3>{nombre}</h3>
                <ul className="list-group list-group-flush">
                    <li className="list-group-item"><b>Profesión: </b>{profesion}</li>
                    <li className="list-group-item"><b>Posición: </b>{posicion}</li>
                    <li className="list-group-item"><b>instagram: </b>{instagram}</li>
                </ul>
                <hr />
                <h5>Biografia</h5>
                <p>{ biografia }</p>
                <button 
                className="btn btn-outline-info"
                onClick={ handleReturn }
                >Return</button>
            </div>
        </div>
    )
}
