import { heroes } from "../data/Heroes";


export const getHeroesByPublisher = ( posicion ) => {

    

    const validposicions = ['Vicepresidente Regional', 'Vicepresidente Regional Platinum', 'Senior Vicepresidente & COC','Senior Vicepresidente' ];

    if (!validposicions.includes(posicion) ){
        throw new Error (`posicion "${ posicion }"No es correcto`)
    }

    return heroes.filter( hero => hero.posicion === posicion );

} 