import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom';
import { Navbar } from '../components/ui/Navbar';
import { MarvelScreen } from '../components/rvp-platinum/MarvelScreen';
import { HerosScreen } from '../components/heroes/HerosScreen';
import { DcScreen } from '../components/rvp/DcScreen';
import { SearchScreen } from '../components/search/SearchScreen';
import { SvpScreen } from '../components/svp/SvpScreen';
import { SvpcocScreen } from '../components/svpcoc/SvpcocScreen';
import { Lideres } from '../components/lideres/Lideres';


export const DashboardRoutes = () => {
    return (
        <>
            <Navbar />
            <div className= "container mt-2">
                <Switch>
                    <Route exact path="/lideres" component={Lideres} />
                    <Route exact path="/" component={Lideres} />
                    <Route exact path="/svpcoc" component={SvpcocScreen} />
                    <Route exact path="/svp" component={SvpScreen} />
                    <Route exact path="/marvel" component={ MarvelScreen } />
                    <Route exact path="/hero/:heroeId" component={ HerosScreen } />
                    <Route exact path="/dc" component={DcScreen} />
                    <Route exact path="/search" component={ SearchScreen } />
                    <Redirect to="/login" />
                </Switch>
            </div>
        </>
    )
}
