import React from 'react'
import { Link } from 'react-router-dom';

export const HeroCard = ({
    id,
    nombre,
    posicion,
    profesion,
    instagram,
    characters
}) => {
    return (
        <div className="card mb-3 ms-3 g-0 mt-3" style={{maxWidth: 350}}>
            <div className="row">
                <div className="col-md-4">
                    <img src={`./assets/heroes/${id}.jpg`} className="card-img" alt={nombre} />
                </div>
                <div className="col-md-8">
                    <div className="card-body">
                        <h5 className="card-title">{nombre}</h5>
                        <p className="card-text">{posicion}</p>
                        <p className="card-text">{profesion}</p>
                        {
                            (profesion !== characters)
                            && <p> {characters}</p>
                        }
                        <p className="card-text">
                            <small className="text-muted"> {instagram}</small>
                        </p>
                        <Link to={`./hero/${id}`}>
                            Mas...
                        </Link>
                    </div>
                </div>
            </div>
        </div>
    )
}
